/* Root */
	Math.root = function(Value, Root) {
		return Math.pow(Value, (1/Root));
	};

/* Greatest Common Divisor */
	Math.gcd = function() {
		for (let index = 0; index < arguments.length; index++) {
			if (typeof arguments[index] != "number") {
				return undefined;
			};
		};
		if (arguments.length == 2) {
			if (arguments[1] == 0) {
				return arguments[0];
			} else {
				return Math.gcd(arguments[1], arguments[0] % arguments[1]);
			};
		} else if (arguments.length > 2) {
			var result = Math.gcd(arguments[0], arguments[1]);
			for (let index = 2; index < arguments.length; index++) {
				result = Math.gcd(result, arguments[index]);
			};
			return result;
		} else {
			return undefined;
		};
	};
	Math.gcf = Math.gcd;
	Math.hcf = Math.gcd;
	Math.gcm = Math.gcd;
	Math.hcd = Math.gcd;

/* Hypotenuse */
	Math.hypotenuse = function(A, B, Query = "h") {
		switch (Query) {
			case "h":
				return Math.sqrt((A * A) + (B * B));
				break;
			case "c":
				return Math.sqrt((A * A) - (B * B));
				break;
			default:
				return undefined;
		};
	};
	Math.antihypotenuse = function(A, B) {
		return Math.hypotenuse(A, B, "c");
	};
